.item {
  background-color: rgb(224, 221, 221);
  display: flex;
  border: 2px solid rgb(180, 180, 177);
  border-radius: 5px;
  margin-bottom: 10px;
  list-style: none;
  width: 160px;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.avatar {
  margin-top: auto;
  margin-bottom: auto;
  height: 45px;
  border: 5px;
}

.name {
  padding-left: 10px;
  font-weight: 700;
  font-size: 20px;
}

.status {
  margin-left: 10px;
  margin-top: auto;
  margin-bottom: auto;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.true {
  composes: status;
  background-color: green;
}

.false {
  composes: status;
  background-color: red;
}
